	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>Stitch is a fashion magazine based around thrifted and vintage clothing that explores current topics related to second-hand items, the history and significance of clothing pieces, the sociology of fashion, as well as providing a styling guide for pairing vintage clothing with modern styles to make them wearable in everyday contexts.</p>\n<p>![null](../assets/img_9398 crop rgb.jpg)</p>\n<p>![](../assets/img_9385 rgb.jpg)</p>\n<p>This image heavy editorial is aimed towards fashion loving young adults. I tried to make every spread dynamic and fun to read by playing with the relationship between text, image and white space.</p>\n<p>![](../assets/img_9382 spreads rgbcolour.jpg)</p>\n<p>I custom designed the typography for half of the headlines including the masthead which was designed to have a handmade feel, almost like it was cut out of paper with small notches in the heavy, geometric shapes creating the letter forms.</p>","frontmatter":{"title":"Stitch magazine","graduate":"Kristen Chung","category":"Editorial Design","category2":"Typography","category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/the-next-website/static/img_9382-covers-rgbcolour-41c69b4ec5a5b3d5ab2a7a8a99657ffe-a7a62.jpg","srcSet":"/the-next-website/static/img_9382-covers-rgbcolour-41c69b4ec5a5b3d5ab2a7a8a99657ffe-5a1a9.jpg 375w,\n/the-next-website/static/img_9382-covers-rgbcolour-41c69b4ec5a5b3d5ab2a7a8a99657ffe-0908c.jpg 750w,\n/the-next-website/static/img_9382-covers-rgbcolour-41c69b4ec5a5b3d5ab2a7a8a99657ffe-a7a62.jpg 1500w,\n/the-next-website/static/img_9382-covers-rgbcolour-41c69b4ec5a5b3d5ab2a7a8a99657ffe-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/stitch-magazine/","name":"/Stitch magazine/"}}

